.container {
  color: #000;
  width: 400px;
  min-height: 300px;
  background-color: #fff;
  border-radius: 300px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  box-shadow: inset 5px 5px 10px #0000004d, inset -5px -5px 10px #0000004d;
}

h1 {
  text-transform: uppercase;
  color: #8a2be2;
  margin-bottom: 10px;
}

.count {
  font-size: 70px;
}

.counters {
  justify-content: center;
  align-items: center;
  gap: 140px;
  display: flex;
}

.btn {
  color: #fff;
  cursor: pointer;
  background-color: #8a2be2;
  border: 1px solid #000;
  outline: none;
  padding: 10px 20px;
  box-shadow: 4px 4px #000;
}

.btn:active {
  box-shadow: none;
  position: relative;
  top: 4px;
  left: 4px;
}

.reset {
  color: #fff;
  cursor: pointer;
  background-color: #000;
  border: none;
  outline: none;
  padding: 3px 10px;
}

.reset:active {
  transform: scale(.95);
}

